/* --------------------------------------------------
   reset
-------------------------------------------------- */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* --------------------------------------------------
   background
-------------------------------------------------- */
body {
  font-family: "Comic Sans MS", "Comic Neue", system-ui, -apple-system,
    BlinkMacSystemFont, sans-serif;
  background: linear-gradient(135deg, #ffd86f, #ff9ec4, #b38bff);
  background-size: 300% 300%;
  animation: ksGradient 18s ease infinite;
  -webkit-font-smoothing: antialiased;
}

@keyframes ksGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* --------------------------------------------------
   layout
-------------------------------------------------- */
.ks-page {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 16px;
}

.ks-card {
  width: 100%;
  max-width: 900px;
  background: rgba(255, 252, 247, 0.96);
  border-radius: 24px;
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.18);
  padding: 24px 32px 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* --------------------------------------------------
   header / status
-------------------------------------------------- */
.ks-header {
  width: 100%;
  max-width: 640px;
  text-align: center;
  margin-bottom: 8px;
}

.ks-title {
  font-size: clamp(2.1rem, 4.6vw, 2.8rem);
  text-shadow: 0 3px 0 rgba(0, 0, 0, 0.25);
  letter-spacing: 0.04em;
  margin-bottom: 4px;
}

.ks-title-icon {
  margin-right: 4px;
}

.ks-status-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 4px;
  font-size: 1rem;
}

.ks-xp {
  font-weight: 700;
  color: #5c3b2a;
}

.ks-lives {
  display: flex;
  gap: 4px;
}

.ks-life {
  font-size: 1.4rem;
  filter: drop-shadow(0 2px 0 rgba(0, 0, 0, 0.2));
}

.ks-life--lost {
  opacity: 0.25;
}

/* --------------------------------------------------
   character art
-------------------------------------------------- */
.ks-character {
  margin-top: 8px;
  margin-bottom: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: clamp(210px, 35vw, 270px);
}

.ks-character-img {
  max-height: 100%;
  max-width: 55%;
  object-fit: contain;
}

/* --------------------------------------------------
   clue / definition
-------------------------------------------------- */
.ks-definition {
  margin-top: 4px;
  margin-bottom: 14px;
  max-width: 520px;
  text-align: center;
  font-size: 0.95rem;
  font-style: italic;
  color: #4b3f3f;
}

.ks-definition-label {
  font-weight: 700;
}

/* --------------------------------------------------
   word row + speaker
-------------------------------------------------- */
.ks-word-row {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 10px;
}

.ks-speak-btn {
  width: 40px;
  height: 40px;
  border-radius: 999px;
  border: none;
  background: #ffe7a3;
  box-shadow: 0 4px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.12s ease, box-shadow 0.12s ease;
}

.ks-speak-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 0 rgba(0, 0, 0, 0.24);
}

.ks-speak-btn:active {
  transform: translateY(1px);
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.18);
}

.ks-masked-word {
  display: flex;
  gap: 8px;
  font-size: clamp(2.1rem, 5vw, 2.6rem);
  letter-spacing: 0.22em;
  text-transform: lowercase;
}

.ks-letter-slot {
  min-width: 26px;
  padding: 2px 0;
  border-bottom: 3px solid #3b2a22;
  text-align: center;
}

/* --------------------------------------------------
   letters tray
-------------------------------------------------- */
.ks-letters {
  margin-top: 12px;
  margin-bottom: 12px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.ks-letter-tile {
  width: 58px;
  height: 58px;
  border-radius: 14px;
  border: none;
  background: #fff7dd;
  box-shadow: 0 5px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
  font-size: 1.6rem;
  text-transform: lowercase;
  transition: transform 0.12s ease, box-shadow 0.12s ease,
    opacity 0.12s ease;
}

.ks-letter-tile:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 7px 0 rgba(0, 0, 0, 0.24);
}

.ks-letter-tile:active:not(:disabled) {
  transform: translateY(1px);
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.2);
}

.ks-letter-tile--used {
  opacity: 0.25;
  transform: translateY(4px);
  box-shadow: none;
  cursor: default;
}

/* --------------------------------------------------
   feedback / reset
-------------------------------------------------- */
.ks-feedback {
  min-height: 22px;
  font-size: 1rem;
  font-weight: 600;
  color: #6b4226;
  text-align: center;
  margin-top: 8px;
}

.ks-reset-btn {
  margin-top: 10px;
  padding: 8px 18px;
  border-radius: 999px;
  border: none;
  background: #ffb347;
  color: #3a220f;
  font-weight: 700;
  cursor: pointer;
  box-shadow: 0 5px 0 rgba(0, 0, 0, 0.2);
  transition: transform 0.12s ease, box-shadow 0.12s ease;
}

.ks-reset-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 7px 0 rgba(0, 0, 0, 0.23);
}

.ks-reset-btn:active {
  transform: translateY(1px);
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.2);
}

/* --------------------------------------------------
   responsive tweaks
-------------------------------------------------- */
@media (max-width: 600px) {
  .ks-card {
    padding: 16px 14px 24px;
    border-radius: 18px;
  }

  .ks-status-row {
    font-size: 0.9rem;
  }

  .ks-character {
    height: 220px;
  }

  .ks-character-img {
    max-width: 65%;
  }

  .ks-letters {
    gap: 8px;
  }

  .ks-letter-tile {
    width: 48px;
    height: 48px;
    font-size: 1.4rem;
  }

  .ks-masked-word {
    letter-spacing: 0.18em;
  }
}
